.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90px;
}

.nav button {
  width: 80%;
  height: 80px;
  border: 0;
  outline: 0;
  background-color: transparent;
  font-size: 1.3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #282828;
  cursor: pointer;
  transition: all 250ms ease-in;
}
.nav button:hover {
  color: #ff0000;
}
.nav span {
  font-size: 12px;
  margin-top: 0.5em;
  color: #282828;
}

@media screen and (max-width: 48rem) {
  .nav {
    display: none;
  }
}
